var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5"},[_vm._m(0),_c('p',{staticClass:"mb-4"},[_vm._v("El contenido de este primer ítem pretende introducirlo y sensibilizarlo frente a la configuración del concepto de contabilidad, sus antecedentes, la importancia que presta en las actividades económicas a las que se dediquen los individuos, bien sea desde lo particular o colectivo, a título personal, o como organización empresarial; mostrando los conceptos básicos generales y su entorno de aplicación y operación, como marco de referencia a nivel mundial de los negocios.")]),_c('p',{staticClass:"mb-5"},[_vm._v("Para comprender un poco de la historia, origen y concepto de la contabilidad como una disciplina indispensable en el desarrollo de las organizaciones o empresas, se inicia por hacer una revisión evolutiva y una identificación de cómo se consolidó dicha disciplina:")]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6"},[_vm._m(1),_c('LineaTiempoD',{staticClass:"color-acento-contenido t1-LineaTiempoD mb-5"},[_c('p',{staticClass:"text-small",attrs:{"numero":"1","titulo":"Imperio Romano"}},[_vm._v("Durante el Imperio romano se aprobaron los registros o asientos en libros de contabilidad. Los banqueros de Roma fueron quienes perfeccionaron técnicas, con el fin de controlar las inversiones que hacían en el ejército para el logro de las conquistas de nuevos territorios.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"2","titulo":"Venecia"}},[_vm._v("En Venecia, entre los siglos VII y VIII, la actividad contable se convirtió en un oficio al servicio de los feudos, quienes llevaban los libros. Asimismo, esto repercutió y llevó a que en las ciudades de Venecia, Florencia y Génova se desarrollaran actividades de lo que se denominó comercio formal, centros de industria y de bancos, lo que les permitió desarrollar técnicas de contabilidad eminentemente de servicios financieros de captación y colocación.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"3","titulo":"Padre contable"}},[_vm._v("En Italia, hacia 1494, el monje franciscano Luca Pacioli propuso el concepto contable de la doble partida, consistente en que todo lo que se tiene proviene de algo, toda transacción tiene un efecto al débito y al crédito, registrando las transacciones de manera cronológica a su acontecimiento; concepto que a la fecha se sigue aplicando y por el cual se le denomina el “padre” de la contabilidad.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"4","titulo":"Estados Unidos"}},[_vm._v("En Estados Unidos, hacia 1920, los bancos y las bolsas de valores comenzaron a exigir estados financieros certificados por contadores públicos independientes a quienes querían acceder a créditos y a realizar actividades bursátiles.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"5","titulo":"Latinoamérica"}},[_vm._v("En nuestra latitud Latinoamérica, los diferentes países, a pesar de lograr su independencia, siguieron aplicando las técnicas contables españolas. En el caso de Colombia, solo en el año 1993, se reglamentó el sistema de contabilidad general, y se expidió, a partir del decreto 2649, el conjunto de normas y principios de la contabilidad general y aceptada -PCGA, nombre que surge de la traducción de las Normas Internacionales de Contabilidad-NIC. ")]),_c('p',{staticClass:"text-small",attrs:{"numero":"6","titulo":"Colombia"}},[_vm._v("Ya en tiempos recientes, se dicta la ley 2420 de 2015, donde se adoptaron, para Colombia, las Normas Internacionales de Información Financiera- NIIF, cuyo objetivo es dar uniformidad a la presentación de la información contable y financiera de las empresas u organizaciones, facilitando su entendimiento y sin tener diferencias según la nacionalidad de quienes las aplican o implementan en su empresa. ")])])],1),_c('div',{staticClass:"col-lg-6"},[_vm._m(2),_c('LineaTiempoD',{staticClass:"color-acento-contenido t1-LineaTiempoD mb-5"},[_c('p',{staticClass:"text-small",attrs:{"numero":"1","titulo":"Ser asociativo"}},[_vm._v("El hombre, como especie, tiene dentro de sus variadas características una en especial y es el ser asociativo, esto es, que necesita relacionarse con los otros individuos; y derivado de esta relación, se asocia con fines específicos, donde el primer objetivo es la supervivencia. Al inicio, el hombre, al igual que otras especies, era dependiente de la naturaleza y nómada, ya que no poseía la capacidad de transformar su entorno y renovar los recursos, lo que hacía que tuviera comportamientos de desplazamiento, máxime en zonas de influencia de las estaciones, que lo dejaban más vulnerable. Las primeras asociaciones derivadas de ese relacionamiento continuo fueron las familias, donde se establecieron roles de desempeño, lo que dio como resultado el origen del trabajo en función de los objetivos comunes.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"2","titulo":"Especialización del trabajo"}},[_vm._v("Esta definición de roles, básicamente, consistió en la consecución de recursos por parte de los integrantes de estas nuevas organizaciones, los cuales debían ser racionalizados y distribuidos. De este ir y venir, las organizaciones se relacionaron con otras organizaciones familiares e intercambiaban productos o instrumentos, dando origen al comercio como trueque. Se consolidan organizaciones más complejas, donde comparten recursos comunes y se desarrolla la especialización del trabajo: unos eminentemente recolectores, otros dedicados a la caza, domesticación de animales, etc. Se dio el desarrollo de nuevas técnicas y las comunidades se fueron asentando poco a poco. Se descubre la renovación de los recursos y se inicia la agricultura, y con ello, la modificación del entorno y el desarrollo sustantivo de utilitarios para la producción de los diferentes recursos. ")]),_c('p',{staticClass:"text-small",attrs:{"numero":"3","titulo":"Conteo y control"}},[_vm._v("Con la especialización laboral y el intercambio, ya los recursos no eran exclusivamente de consumo para la familia, sino que servían para intercambiar por otros, por lo cual se necesitó llevar técnicas de conteo, control y registro, lo que dio origen a la contabilidad como la disciplina que cumple con estos preceptos, y con ella, la escritura en paredes o tablillas, junto con la acumulación de recursos, tal como lo evidencia la historia.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"4","titulo":"Negociación"}},[_vm._v("Este ejercicio, con el tiempo, se masificó y se adoptó en todas las organizaciones sociales y a todas las prácticas a las que se dedicaban los individuos, dando origen a grandes civilizaciones, donde cada una aportó diferentes instrumentos para la administración y tenencia de los recursos. Se podían concretar las negociaciones y luego la compensación, se podía entregar uno de los recursos en el momento y que la contraparte recibiera la contraprestación en otro momento, prácticas que debían evidenciarse por medio de registros.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"5","titulo":"Financiamiento"}},[_vm._v("Civilizaciones como la de los sumerios y mesopotámicos realizaron grandes aportes a la contabilidad de los recursos, como la invención de sistemas de pesos y medidas para racionalizar el intercambio de cantidades de un recurso, la creación del pagaré como evidencia ante un intercambio pendiente de compensación, la letra de cambio que evidencia el acto de fe donde se declara la obligación de alguien de pagar o compensar por un bien recibido en un tiempo determinado, así como las prácticas de cobranza sobre esos derechos adquiridos por una transacción realizada en el pasado que tiene el registro y que está pendiente de cumplirse o ya se cumplió; todos los cuales constituyen esquemas iniciales de financiamiento tal como hoy se pueden realizar.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"6","titulo":"Sostenibilidad"}},[_vm._v("En muchas otras civilizaciones del mundo, se adoptaron esquemas y sistemas similares en las organizaciones sociales, donde el papel predominante de la contabilidad fue sustantivo para el cumplimiento de los objetivos y la sostenibilidad de dichas organizaciones. ")])])],1)]),_c('p',{staticClass:"mb-5"},[_vm._v("Para cerrar el ciclo de la evolución e historia contable, cabe resaltar que desde hace años atrás se utilizan recursos de software que facilitan y agilizan las actividades contables, los cuales son parametrizados de acuerdo con las actividades de objeto social, bien sean bienes, productos o servicios.")]),_vm._m(3),_c('Separador'),_vm._m(4),_vm._m(5),_c('p',{staticClass:"mb-5"},[_vm._v("Para dar respuesta, pareciera inicialmente que no, pues son actividades meramente administrativas que por ley se exigen y son parte de los procesos de ordenamiento de los recursos financieros con que se opera en las empresas, pero si se analiza el propósito u objetivo de la contabilidad, el cual es describir lo que ha ocurrido en la organización en tiempos atrás y determinar el estado actual desde la óptica financiera como un efecto de las actuaciones de dicho pasado, entonces sí se puede decir que es todo un lenguaje descriptivo que hay que aprender a leer e interpretar para saber tomar nuevas actuaciones, tal como sucede con cualquier otro idioma, donde, en la medida en que se interpretan las cosas, se sabe cómo interactuar. ")]),_vm._m(6),_c('p'),_vm._m(7),_c('Separador'),_vm._m(8),_c('p',{staticClass:"mb-5"},[_vm._v("La contabilidad tiene como finalidad objetiva suministrar información de la evolución histórica hasta la situación actual, respecto de la posición económica y financiera de la organización o empresa donde se aplique, en relación al desarrollo de sus actividades u objeto social, así como de todas las actuaciones o transacciones, ya que es pertinente su conocimiento para la toma de decisiones por parte de los interesados.")]),_c('div',{staticClass:"py-5",staticStyle:{"background-color":"#E8F0F8"}},[_c('div',{staticClass:"row justify-content-around"},[_vm._m(9),_c('div',{staticClass:"col-lg-6 d-flex align-items-center"},[_c('div',[_vm._m(10),_c('p',{staticClass:"mb-5"},[_vm._v("Para reconocer la amplitud de conceptos sobre la temática de objeto de la contabilidad, puede consultar el Marco Conceptual para la Información Financiera del Consejo de Normas Internacionales de Contabilidad (2018):")]),_c('a',{staticClass:"boton color-acento-botones texto-blanco",attrs:{"href":_vm.obtenerLink('/downloads/Anexo-Marco-Conceptual-para-la-informacion-financiera.pdf'),"target":"_blank"}},[_c('span',[_vm._v("Descargar")]),_c('i',{staticClass:"fas fa-file-download"})])])])])]),_c('Separador'),_vm._m(11),_vm._m(12),_c('h3',[_vm._v("A. Interesados o usuarios internos")]),_c('p',{staticClass:"mb-5"},[_vm._v("Se encuentran los accionistas o propietarios de la empresa, los directivos o jefes, los empleados directos e indirectos, etc. Los cuales se detallan a continuación: ")]),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-1"}),_c('div',{staticClass:"col-lg-5 d-flex"},[_c('div',{staticClass:"tarjeta tarjeta-flip color-primario",on:{"mouseover":function($event){_vm.indicadorTarjetaFlip = false}}},[(_vm.indicadorTarjetaFlip)?_c('div',{staticClass:"indicador--hover"}):_vm._e(),_c('div',{staticClass:"tarjeta-flip__contenedor"},[_c('div',{staticClass:"tarjeta-flip__img",style:({'background-image': ("url(" + (require('@/assets/curso/tema1/img04.svg')) + ")")})}),_vm._m(13)])])]),_c('div',{staticClass:"col-lg-5 d-flex"},[_c('div',{staticClass:"tarjeta tarjeta-flip color-acento-contenido",on:{"mouseover":function($event){_vm.indicadorTarjetaFlip = false}}},[(_vm.indicadorTarjetaFlip)?_c('div',{staticClass:"indicador--hover"}):_vm._e(),_c('div',{staticClass:"tarjeta-flip__contenedor"},[_c('div',{staticClass:"tarjeta-fl