os que suministran podrán ser pagadas al vencimiento o en un periodo más corto al que pactaron al momento de la negociación para el suministro de las mismas.")])]),_c('div',{staticClass:"t1-tab1 tarjeta color-primario--borde p-4 py-5",attrs:{"titulo":"Clientes"}},[_c('h4',[_vm._v("Clientes")]),_c('p',{staticClass:"mb-0"},[_vm._v("Son los compradores o adquirientes de los productos o servicios que la empresa oferta, pueden ser consumidores directos o indirectos; directos, si son consumidores del bien o lo utilizan como materia prima para otros productos; e indirectos, si son compradores que buscan revender los productos de la empresa a cambio de un margen de beneficio. A los clientes les interesa contar con información contable de la empresa para saber sobre la continuidad y viabilidad de la empresa en el tiempo, cómo ha evolucionado y por tanto sobre su continuidad, especialmente cuando tienen compromisos a largo plazo o dependen comercialmente de ella. ")])]),_c('div',{staticClass:"t1-tab1 tarjeta color-primario--borde p-4 py-5",attrs:{"titulo":"Gobierno"}},[_c('h4',[_vm._v("Gobierno")]),_c('p',{staticClass:"mb-4"},[_vm._v("El Estado y sus entidades gubernamentales son las que determinan las regulaciones para incentivar y dar continuidad a las empresas y organizaciones, por ello se preocupan por la disponibilidad y acceso a los recursos económicos para las empresas, velando por el funcionamiento de las mismas en capacidad y desarrollo; para tal fin, requieren información contable y financiera periódicamente de las empresas, para identificar y establecer estadísticas de actividad por parte de las empresas que se dedican a lo mismo, según su tamaño, por el sector al que pertenecen, por los niveles de endeudamiento y posición financiera que tengan, etc., información que puede ser de forma desagregada o de forma agregada en variables macroeconómicas; con esta información, podrá el gobierno tomar decisiones de si debe intervenir para fortalecer los mercados o para fortalecer la actividad económica en sí, con tecnologías, protección de mercados, etc., así como complementar la política para el desarrollo empresarial. ")]),_c('p',{staticClass:"mb-0"},[_vm._v("Por otro lado, al gobierno le interesa la información contable para determinar los niveles de impuestos que recibirá, fruto de la carga impositiva que se haya aprobado, pues de esos recursos es que realiza lo expuesto en el párrafo anterior.")])]),_c('div',{staticClass:"t1-tab1 tarjeta color-primario--borde p-4 py-5",attrs:{"titulo":"Entidades de control"}},[_c('h4',[_vm._v("Entidades de control")]),_c('p',{staticClass:"mb-0"},[_vm._v("Por su parte, las entidades de control son las encargadas de verificar que las actuaciones de la empresa obedezcan a la normatividad vigente, que las empresas se dediquen y mantengan actividades lícitas, que no realicen actos comerciales por fuera de la ley; asimismo, que la información contable efectivamente exprese los hechos ocurridos en la empresa y que se estén declarando y pagando los impuestos de rigor, como el cumplimiento de sus obligaciones contractuales con los trabajadores y demás acreedores que tuviere, dentro de un marco de buenas prácticas contables. Por ello, periódicamente, mínimo una vez al año, hay que presentarles dicha información contable. ")])]),_c('div',{staticClass:"t1-tab1 tarjeta color-primario--borde p-4 py-5",attrs:{"titulo":"Público en general"}},[_c('h4',[_vm._v("Público en general")]),_c('p',{staticClass:"mb-0"},[_vm._v("La sociedad en general y todas las personas, de manera directa o indirecta, son afectadas por la operación y actividad de las empresas, por lo cual pueden requerir información acerca de su configuración, legalidad, su desarrollo y evolución, así como de sus proyecciones y presuntos alcances, ya que afectan el entorno de los vecinos o comunidad en general.")])])]),_c('p',{staticClass:"mb-5"},[_vm._v("A continuación, se verifican y se organizan los interesados en la información contable, a través del 